.btn-span {
  position: relative;
  top: 1px;
}
.hide {
  display: none !important;
}
.adeed-home .second-title {
  display: none;
}
section#adeed-home {
  background: #f5f5f5;
  padding: 50px 0;
  margin-bottom: 50px;
}
section#adeed-home .section-title {
  font-weight: 700;
  font-size: 1.6em;
  margin-bottom: 15px;
}
section#adeed-home .section-content {
  font-size: 1.1em;
  color: #a09e9e;
}
@media screen and (max-width: 992px) {
  section#adeed-home {
    text-align: center;
  }
  .photo-col {
    display: none;
  }
}
.feedback-swiper .swiper-pagination-bullet {
  background-color: #d8c1c1;
}
.feedback-swiper span.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: #f7f7f7;
}
.adeed-call-center {
  direction: ltr;
  text-align: left;
  unicode-bidi: bidi-override;
}
.site,
.mobile-menu {
  transition: all 500ms;
}
.mobile-menu,
.mobile-menu-overlay {
  display: none;
}
body.mobile-menu-expanded {
  overflow: hidden;
}
body.mobile-menu-expanded .site {
  min-height: 100vh;
  position: relative;
  z-index: 999;
}
body.mobile-menu-expanded .mobile-menu {
  display: block;
  background: #137daf;
  min-height: 100vh;
  width: 80%;
  overflow: hidden;
  position: fixed;
  top: 0;
  z-index: 9999;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}
body.mobile-menu-expanded .mobile-menu-overlay {
  cursor: pointer;
  display: block;
  background: rgba(0, 0, 0, 0.4);
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
}
body.mobile-menu-expanded.menu-lang-ar .site {
  transform: translate(80%);
}
body.mobile-menu-expanded.menu-lang-ar .mobile-menu {
  left: 0;
}
body.mobile-menu-expanded.menu-lang-en .site {
  transform: translate(-80%);
}
body.mobile-menu-expanded.menu-lang-en .mobile-menu {
  right: 0;
}
button.toggle-mobile-menu {
  background: var(--accent-color);
  color: #fff;
  border: none;
  border-radius: 5px;
  line-height: 1;
  display: none;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  width: 40px;
  height: 36px;
}
@media screen and (min-width: 1024px) {
  .web-menu {
    display: block;
  }
  button.toggle-mobile-menu {
    display: none;
  }
  .mobile-menu,
  .mobile-menu-overlay {
    display: none;
  }
}
@media screen and (max-width: 1024px) {
  .web-menu {
    display: none;
  }
  button.toggle-mobile-menu {
    display: flex;
  }
}
.web-page-content {
  font-family: var(--font-family) !important;
  font-size: 1.1em !important;
  margin-bottom: 20px;
}
.web-page-content * {
  font-family: var(--font-family) !important;
  font-size: 1em !important;
}
.os-not-ios #feedback,
.os-not-ios #promise {
  background-attachment: fixed !important;
}
section#top-categories {
  padding: 50px 0;
  text-align: center;
}
.top-wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.top-wrapper a {
  flex: 0 0 33.333333%;
  margin-top: 15px;
  text-decoration: none;
}
span.cat-photo {
  display: block;
  opacity: 0.7;
}
span.cat-photo img {
  width: 56px;
}
span.cat-name {
  color: var(--dark-text);
  display: block;
  text-align: center;
  margin-top: 10px;
  font-size: 1.1em;
  text-decoration: none;
}
@media screen and (max-width: 575px) {
  h3.section-title {
    font-size: 1.6em;
  }
  .top-wrapper a {
    flex: 0 0 50%;
  }
  span.cat-photo img {
    width: 36px;
  }
  span.cat-name {
    font-size: 1em;
  }
}
a.main-cat {
  display: block;
  position: relative;
  margin-top: 15px;
  border-radius: 10px;
  overflow: hidden;
}
span.all-photo img {
  display: block;
  width: 100%;
}
span.all-name {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 17, 17, 0.47);
  color: #fff;
  font-weight: 700;
  font-size: 1.2em;
  padding: 10px;
  text-align: center;
  transition: all 500ms;
}
span.all-name:hover {
  background-color: rgba(17, 17, 17, 0.54);
}
.part-sub-title {
  color: #454647;
  font-weight: 700;
  font-size: 1.4em;
  margin-bottom: 15px;
}
.sc-content {
