  font-size: 1em !important;
}
.sc-content * {
  font-family: var(--font-family) !important;
  font-size: 1em !important;
}
.sc-content p {
  margin-bottom: 0 !important;
}
.top-news-item {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 30px;
}
.top-news-item .news-photo {
  width: 340px;
}
.top-news-item .news-info {
  flex: 1;
}
.top-news-item .news-photo img {
  width: 100%;
}
.top-news-item .new-title {
  font-weight: 700;
