  }
}
.news-item {
  margin-bottom: 30px;
  border: solid 1px #ddd;
  overflow: hidden;
}
.news-item .news-photo img {
  width: 100%;
}
.news-item .new-title {
  font-weight: 700;
  font-size: 1.15em;
  margin-bottom: 10px;
